.note {
    max-width: 250px;
    max-height: max-content;

    display: flex;
    flex-direction: column;

    border: 2px black solid;
    border-radius: 7px;
}

.note-content {
    display: flex;
    flex-direction: column;
}

.note-content-subject {
    height: 30px;
    font-size: 20px;
    width: 100%;
    padding: 8px 0 30px 10px;
    border-bottom: 2px rgb(92, 92, 92) solid;
}

.note-content-text {
    display: block;
    display: -webkit-box;
    line-height: 1.5rem;
    max-height: calc(1.5rem * 8.5);
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
    text-overflow: ellipsis;
    
    font-size: 18px;
    padding: 8px;
}

.note-data {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    height: 30px;
    margin-top: 10px;
    padding: 6px 4px 2px 4px;
    font-size: 16px;
    border-top: 1px rgb(92, 92, 92) solid;
}

.note-footer {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 10px 0;
    background-color: rgb(82, 82, 82);
}

.note-footer i {
    display: inline-block;
    cursor: pointer;
    margin-right: 10px;
}

.note-footer i:first-child{
    margin-right: 20px;
}
